}

.u-timeline__item:not(:last-child) {
  padding-bottom: 1.5rem;
  margin-bottom: 1.5rem;
}

.u-timeline__item::before {
  position: absolute;
  top: 3.5rem;
  bottom: 0;
  left: 0;
  height: auto;
  border-left: 0.1875rem solid #e7eaf3;
  content: "";
}

.u-timeline__icon {
  position: absolute;
  left: -.875rem;
}

.u-timeline__heading {
